        <div class="actionItem" @click="hideActionMaskFunction">取消</div>
      </div>
    </div>
    <div class="chart_tap">
      <div class="rich_gold" :class="showActive == 1 ? 'active':''" @click="switchChart(1)">单位净值
        <div class="wrie"></div>
      </div>
      <div class="spot_gold" :class="showActive == 2 ? 'active':''" @click="switchChart(2)">累计净值
        <div class="wrie"></div>
      </div>
      <div class="spot_gold" :class="showActive == 3 ? 'active':''" @click="switchChart(3)">收益走势
        <div class="wrie"></div>
      </div>
    </div>
    <div class="container">
      <div v-if="!showIncomeRate" class="showInfo">
        <div class="item">
          <div class="dot"></div>
          <div class="text">近一月:
            <p
              :style="data.value[data.value.length -1] > 0?'color: rgb(255,72,72);':'color: rgb(51,143,47);'"
            >{{data.value[data.value.length -1]}}%</p>
          </div>
        </div>
        <div class="item">
          <div class="dot grey"></div>
          <div class="text">同类平均:
            <p
              :style="contrast.value[contrast.value.length -1] > 0?'color: rgb(255,72,72);':'color: rgb(51,143,47);'"
            >{{contrast.value[contrast.value.length -1]}}%</p>
          </div>
        </div>
        <div class="item" @click="showActionMaskFunction">
          <div class="dot blueLight"></div>
          <div class="text">
            {{compareName}}:
            <p
              :style="compare.value[compare.value.length -1] > 0?'color: rgb(255,72,72);':'color: rgb(51,143,47);'"
            >{{compare.value[compare.value.length -1]}}%</p>
          </div>
