            >{{showInfodata[2]}}%</p>
          </div>
        </div>
      </div>
      <div>
        <Chart
          ref="lineChart"
          :active="active"
          :data="data"
          :contrast="contrast"
          :compare="compare"
          :compareName="compareName"
          :tradeList="tradeList"
        />
      </div>
    </div>
    <div style="background-color: #ffffff;">
      <div class="date_box">
        <div :class="isActive=='1'?'active':''" @click="changeActive('1')">近一个月</div>
        <div :class="isActive=='2'?'active':''" @click="changeActive('2')">近三个月</div>
        <div :class="isActive=='3'?'active':''" @click="changeActive('3')">近半年</div>
        <div :class="isActive=='4'?'active':''" @click="changeActive('4')">近一年</div>
        <div :class="isActive=='5'?'active':''" @click="changeActive('5')">更多</div>
      </div>
