<script>
// @ is an alias to /src
import Chart from "@/components/bs-line-chart";
import { mockData, mockDataOneYear } from "@/mockData/data";
import dayjs from "dayjs";
// import { setTimeout } from 'timers';

export default {
  name: "home",
  components: {
    Chart
  },
  data() {
    return {
      showActionSheet: false,
      showIncomeRate: false,
      showInfodata: [],
      isActive: "1",
      showActive: "3",
      active: 3,
      compareName: "沪深300",
      compareId: "hs300",
      showChart: true,
      compareList: [
        {
          id: "hs300",
          name: "沪深300"
        },
        {
          id: "zz500",
          name: "中证500"
        },
        {
          id: "szzs",
          name: "上证指数"
        },
        {
          id: "szcz",
          name: "深证成指"
        },
        {
          id: "zxbz",
          name: "中小板指"
        },
        {
          id: "cybz",
          name: "创业板指"
        }
      ],
      data: {
        date: [],
        value: []
      },
      contrast: {
        date: [],
        value: []
      },
      compare: {
        date: [],
        value: []
      },
      tradeList: [
        {
          date: "2019-08-09",
          type: "buy"
        },
        {
          date: "2019-08-20",
          type: "buy"
        },
        {
          date: "2019-10-09",
          type: "buy"
        },
        {
          date: "2020-01-09",
          type: "buy"
        },
        {
          date: "2019-09-20",
          type: "sale"
        },
        {
          date: "2020-01-20",
          type: "sale"
        }
      ]
    };
  },
  watch: {
    showInfodata(newV) {
      this.showIncomeRate = true;
      return newV;
    }
  },
  mounted() {
    this.fetchData();
  },
  methods: {
    selectCompareFund(index) {
      console.log(index);
      this.compareName = this.compareList[index].name;
      this.compareId = this.compareList[index].id;
      this.showActionSheet = false;
      this.fetchData();
    },
    showActionMaskFunction() {
      this.showActionSheet = true;
    },
    hideActionMaskFunction() {
      this.showActionSheet = false;
    },
    updateShowInfo(yList) {
      this.showInfodata = yList;
    },
    switchChart(num) {
      this.showActive = num;
    },
    changeActive(num) {
      this.isActive = num;
      this.fetchData();
    },
    test() {
      console.log("fixed click!");
      this.showIncomeRate = false;
      // this.$refs.lineChart.update();
    },
    fetchData() {
      this.data = {
        date: [],
        value: []
      };
      this.contrast = {
        date: [],
        value: []
      };
      this.compare = {
        date: [],
        value: []
      };
      let data;
      if (this.isActive == 1) {
        data = mockData(this.compareId);
      } else if (this.isActive == 4) {
        data = mockDataOneYear(this.compareId);
      }

      let fundData = data.fund.data;
      let similarData = data.similar.data;
      let compareData = data.compare.data;

      for (let i in fundData) {
        this.data.date.push(dayjs(fundData[i][0]).format("YYYY-MM-DD"));
        this.data.value.push(fundData[i][1]);
      }

      for (let i in similarData) {
        this.contrast.date.push(dayjs(similarData[i][0]).format("YYYY-MM-DD"));
        this.contrast.value.push(similarData[i][1]);
      }

      for (let i in compareData) {
        this.compare.date.push(dayjs(compareData[i][0]).format("YYYY-MM-DD"));
        this.compare.value.push(compareData[i][1]);
      }
    }
  }
};
</script>

