<template>
  <div ref="lineChart" style="width: 100%;height:1.75rem" :style="{ height: this.height}"></div>
</template>
<script>
// import { formatnumber } from "@/utils/utils";
import dayjs from "dayjs";
// import { setTimeout } from "timers";
let that;
let yMax = "";
let yMin = "";
let y1Max = "";
let y1Min = "";
let y2Max = "";
let y2Min = "";
let y3Max = "";
let y3Min = "";
let minX = 0;
let maxX = 0;

export default {
  props: {
    data: {
      required: true
    },
    //收益走势的第二条线数据,只有收益走势传
    contrast: {},
    compare: {},
    compareName: {},
    // 不同的走势图标识
    active: {},
    themeColor: {
      default: "#eb6b69"
    },
    height: {
      default: "50vw"
    },
    toFixed: {
      default: 3
    },
    marginLeft: {
      default: 45
    },
    isMmf: {
      default: false
    },
    fromGold: {
      default: false
    },
    dataLength: {
      default: 3
    },
    combinationName: {},
    darkMode: {
      default: false
    },
    tradeList: {}
  },
  data() {
    return {
      tipPositon: {}
    };
  },
  mounted() {
    that = this;
    console.log("mounted!");
    this.render(this.data, this.options);
  },
  beforeDestroy() {
    if (this.chart && this.chart.destroy) {
      this.chart.destroy();
    }
  },
  computed: {
    options() {
      return {
        themeColor: this.themeColor,
        toFixed: this.toFixed,
        marginLeft: this.marginLeft
      };
    },
    // hint() {
    //   return `<span style="font-size:10px">${
    //     data.date[this.x]
    //   }</span><br>${Number(this.y).toFixed(2)}`;
    // },
    getSeries() {
      let series = [
        {
          // type: 'areaspline',
          data: this.data.value,
          fillColor: {
            //设置折线下方背景渐变
            // linearGradient: { x1: 0, y1: 0, x2: 0, y2: 1 },
            // stops: [
            //   [0, "rgba(255,184,184,0.50)"],
            //   [0.3, "rgba(255,213,213,0.50)"],
            //   [1, "rgba(255,255,255,0.50)"]
            // ]
            linearGradient: { x1: 0, x2: 0, y1: 0, y2: 1 },
            //设置背景颜色渐变,博时智投的背景色和基金黄金的背景色
            // stops: this.active == 6 ? [[0.3, "rgba(255, 59, 48, 0.5)"], [1, this.$highcharts.Color("rgba(255, 59, 48, 0.5)").setOpacity(0).get("rgba")]] :
            //   [[0.3, "rgba(255,157,150,0.25)"], [1, this.$highcharts.Color("rgba(255,157,150,0.25)").setOpacity(0).get("rgba")]]
            stops:
              this.active == 6
                ? [
                    [0, "rgba(255,157,150,0.25)"],
                    [
                      0.8,
                      this.$highcharts
                        .Color("rgba(255,157,150,0.25)")
                        .setOpacity(0)
                        .get("rgba")
                    ]
                  ]
                : [
                    // [0.3, "rgba(255,157,150,0.25)"],
                    // [
                    //   1,
                    //   this.$highcharts
                    //     .Color("rgba(255,157,150,0.25)")
                    //     .setOpacity(0)
                    //     .get("rgba")
                    // ]
                  ]
          },
          marker: {},
          dataLabels: {},
          color: "#eb6b69",
          threshold: null,
          lineWidth: 1,
          showInLegend: false,
          pointStart: 0
        }
      ];
      if (this.active == 3 || this.active == 6) {
        if (!this.contrast) {
          console.log("this.contrast.value is empty!");
          series.push({
            type: "line",
            color: "#cccccc",
            lineWidth: 1,
            data: "--"
          });
        } else {
          series.push({
            type: "line",
            color: this.active == 6 ? "#ff1338" : "#cccccc",
            lineWidth: 1,
            data: this.contrast.value
          });
        }
        // 比较指数
        if (!this.compare) {
          console.log("this.compare.value is empty!");
          series.push({
            type: "line",
            color: "#cccccc",
            lineWidth: 1,
            data: "--"
          });
        } else {
          series.push({
            type: "line",
            color: "#4a90e2",
            lineWidth: 1,
            data: this.compare.value
          });
        }
      }
      return series;
    },
    Xposition() {
      let Xposition = new Array();
      Xposition.push(0);
      if (this.data.date.length % 2 == 0) {
        Xposition.push(parseInt(this.data.date.length / 2));
      } else {
        Xposition.push(parseInt(this.data.date.length / 2) - 1);
      }
      Xposition.push(this.data.date.length - 1);
      // console.log(Xposition)
      return Xposition;
    }
  },
  watch: {
    data() {
      console.log("watch!");
      this.render(this.data, this.options);
    },
    darkMode() {
      console.log("line-chart watch---->", this.darkMode);
      this.render(this.data, this.options);
    }
  },

  methods: {
