            y1Min = Math.min(...data.value);

            y2Max = Math.max(...this.contrast.value);
            y2Min = Math.min(...this.contrast.value);

            y3Max = Math.max(...this.compare.value);
            y3Min = Math.min(...this.compare.value);
            let maxList = [y1Max, y2Max, y3Max];
            let minList = [y1Min, y2Min, y3Min];
            yMax = Math.max(...maxList);
            yMin = Math.min(...minList);
          } else {
            yMax = Math.max(...data.value) + 0.5;
            yMin = Math.min(...data.value) - 0.5;
          }
        } else {
          yMax = Math.max(...data.value) + 0.01;
          yMin = Math.min(...data.value) - 0.01;
