        let hadBuyLabels = false;
        let hadSaleLabels = false;
        for (let i in this.data.value) {
          let item = null;

          if (this.data.value[i] == y1Max) {
            maxX = i;
            item = {
              y: this.data.value[i],
              id: "max"
              // dataLabels: {
              //   shape: "connector",
              //   enabled: true,
              //   // backgroundColor: "rgba(0, 0, 0, 0.75)",
              //   style: {
              //     fontSize: '0.6rem',
              //     color: "#aaaaaa",
              //     textOutline: "none"
              //   },
              //   // y: -36,
              //   // formatter() {
              //   //   return `<div style="position: relative">${y1Max}%<div style="position: absolute; bottom: -12px; left: 50%; width: 1px; height: 12px; background-color: #cccccc;"></div></div>`
              //   // },
              //   // useHTML: true
              // }
            };
          }

          if (this.data.value[i] == y1Min) {
            minX = i;
            item = {
              y: this.data.value[i],
              id: "min"
              // dataLabels: {
              //   shape: "connector",
              //   enabled: true,
              //   style: {
              //     fontSize: '0.6rem',
              //     color: "#aaaaaa",
              //     textOutline: "none"
              //   },
              //   // y: 36,
              //   // formatter() {
              //   //   return `<div style="position: relative; font-size:0.3rem; ">${y1Min}%<div style="position: absolute; top: -12px; left: 50%; width: 1px; height: 12px; background-color: #cccccc;"></div></div>`
              //   // },
              //   // useHTML: true
              // }
