          }
          this.data.value[i] = item ? item : this.data.value[i];

          // 处理买入卖出
          if (this.tradeList) {
            for (let j in this.tradeList) {
              if (this.data.date[i] == this.tradeList[j].date) {
                if (this.tradeList[j].type == "buy") {
                  if (this.data.value[i].y) {
                    this.data.value[i].marker = {
                      fillColor: "red",
                      lineWidth: 0,
                      radius: 2,
                      lineColor: null // inherit from series
                    };
                    this.data.value[i].dataLabels = {
                      enabled: hadBuyLabels ? false : true,
                      shape: "callout",
                      backgroundColor: "#ff4848",
                      borderRadius: 5,
                      borderWidth: 0,
                      padding: 0,
                      allowOverlap: true,
                      style: {
                        // fontSize: '0.2rem',
                        color: "#FFFFFF",
                        textOutline: "none",
                        fontWeight: "400"
                      },
                      format: '<div style="transform: scale(0.7);">买入</div>',
                      useHTML: true,
                      y: -10
                    };
                  } else {
                    let object = {
                      y: this.data.value[i],
                      marker: {
                        fillColor: "red",
                        lineWidth: 0,
                        radius: 2,
                        lineColor: null // inherit from series
                      },
                      dataLabels: {
                        enabled: hadBuyLabels ? false : true,
                        shape: "callout",
                        backgroundColor: "#ff4848",
                        borderRadius: 5,
                        borderWidth: 0,
                        padding: 0,
                        allowOverlap: true,
                        style: {
                          // fontSize: '0.2rem',
                          color: "#FFFFFF",
                          textOutline: "none",
                          fontWeight: "400"
                        },
                        format:
                          '<div style="transform: scale(0.7);">买入</div>',
                        useHTML: true,
                        verticalAlign: "bottom",
                        y: -10
                      }
                    };
                    if (!hadBuyLabels) {
                      hadBuyLabels = true;
                    }
                    this.data.value[i] = object;
                  }
                }

                if (this.tradeList[j].type == "sale") {
                  if (this.data.value[i].y) {
                    this.data.value[i].marker = {
                      fillColor: "#4ab3e2",
                      lineWidth: 0,
                      radius: 2,
                      lineColor: null // inherit from series
                    };
                    this.data.value[i].dataLabels = {
                      enabled: hadSaleLabels ? false : true,
                      shape: "callout",
                      backgroundColor: "#4ab3e2",
                      borderRadius: 5,
                      borderWidth: 0,
                      padding: 0,
                      allowOverlap: true,
                      style: {
                        // fontSize: '0.2rem',
                        color: "#FFFFFF",
                        textOutline: "none",
                        fontWeight: "400"
                      },
                      format: '<div style="transform: scale(0.7);">卖出</div>',
                      useHTML: true,
                      y: -10
                    };
                  } else {
                    let object = {
                      y: this.data.value[i],
                      marker: {
                        fillColor: "#4ab3e2",
                        lineWidth: 0,
                        radius: 2,
                        lineColor: null // inherit from series
                      },
                      dataLabels: {
                        enabled: hadSaleLabels ? false : true,
                        shape: "callout",
                        backgroundColor: "#4ab3e2",
                        borderRadius: 5,
                        borderWidth: 0,
                        padding: 0,
                        allowOverlap: true,
                        style: {
                          // fontSize: '0.2rem',
                          color: "#FFFFFF",
                          textOutline: "none",
                          fontWeight: "400"
                        },
                        format:
                          '<div style="transform: scale(0.7);">卖出</div>',
                        useHTML: true,
                        verticalAlign: "bottom",
                        y: -30
                      }
                    };
                    if (!hadSaleLabels) {
                      hadSaleLabels = true;
                    }
                    this.data.value[i] = object;
                  }
                }
              }
            }
          }
        }
        console.log(maxX, minX);
      }
      /**
       * active:1--单位净值
       * active:2--累计净值
       * active:3--收益走势
       * active:4--七日年化
       * active:5--万份收益
       * active:6--博时智投
       *
       */
      //控制y轴的上下间距,线的起伏波动

