        title: false,
        credits: {
          enabled: false
        },
        legend: {
          enabled: false
        },
        chart: {
          animation: false,
          type: "area",
          spacingBottom: 10,
          spacingTop: 20,
          spacingLeft: 10,
          spacingRight: 15,
          margin: null,
          backgroundColor: this.darkMode ? "#333333" : "#ffffff"

          // zoomType: 'x',
          // panning: true,
          // panKey: 'shift'
        },
        annotations: [
          {
            //visible: true, by default the annotation is visible
            labelOptions: {
              shape: "connector",
              backgroundColor: "#929399",
              borderColor: "#929399",
              justify: false,
              crop: false,
              style: {
                color: "#929399",
                fontSize: "0.8em",
                textOutline: "1px white"
              }
            },
            labels: [
              {
                point: "max",
                align: "right",
                distance: 20,
                // verticalAlign: "bottom",
                text: `${y1Max}%`
              },
              {
                point: "min",
                align: "left",
                // x: 30,
                // y: 0,
                distance: 20,
                // verticalAlign: "top",
                text: `${y1Min}%`
              }
            ]
          }
        ],
        xAxis: {
          title: false,
          lineColor: "#ccc",
          lineWidth: 0,
          tickLength: 0,
          minorGridLineWidth: 0,
          gridLineWidth: 0,
          gridLineColor: "#000",
          startOnTick: false,
          // tickInterval: 10, // x轴间距
          tickPositions: this.Xposition,
          // tickPixelInterval: 155,
          minPadding: 0,
          maxPadding: 0,
          crosshair: {
            width: 1,
            color: themeColor,
            dashStyle: "shortdot",
            zIndex: 3
          },
          labels: {
            style: {
              color: "#999",
              fontSize: "10px"
            },
            formatter() {
              let date = data.date[this.value];
              if (date) {
                date = dayjs(date, "YYYY-MM-DD").format("MM-DD");
              }
              return date;
            }
          }
        },
        yAxis: {
          title: false,
          lineColor: "#ccc",
          lineWidth: 0,
          minorGridLineWidth: 0,
          gridLineWidth: 1,
          gridLineColor: "#eee",
          // tickAmount: 5,
          tickPositioner: function() {
            var positions = [],
              // zeroPoint = 0,
              tick = Math.floor(yMin),
              increment = Math.ceil((yMax - yMin) / 4),
              upDiff = 0,
              downDiff = 0;
            console.log("increment----->", increment);
            for (tick; tick - increment <= yMax; tick += increment) {
              console.log("tick--->", tick);
              positions.push(tick);
            }
            for (let i in positions) {
              if (positions[i] > 0 && positions[i - 1] < 0) {
                downDiff = Math.abs(positions[i - 1]);
                upDiff = Math.abs(positions[i]);
              }
            }
            console.log(upDiff, downDiff);
            console.log("positions---->", positions);
            if (upDiff > downDiff) {
              for (let i in positions) {
                positions[i] = positions[i] - upDiff;
              }
            } else {
              for (let i in positions) {
                positions[i] = positions[i] + downDiff;
              }
            }
            if (positions[0] > yMin) {
              positions.unshift(positions[0] - increment);
            }
            if (positions[positions.length - 1] < yMax) {
              positions.push(positions[positions.length - 1] + increment);
            }
            console.log("positions---->", positions);
            return positions;
          },
          max: null,
          min: null,
          crosshair: {
            width: 1,
            color: themeColor,
            dashStyle: "shortdot",
            zIndex: 3
          },
          labels: {
            style: {
              color: "#999",
              fontSize: "10px"
            },
            formatter() {
              //y轴小数位
              if (that.active == 3 || that.active == 4 || that.active == 6) {
                return this.value.toFixed(2) + `%`;
              } else {
                return this.value.toFixed(2);
              }
            }
          }
        },
        tooltip: {
          //提示框的背景颜色
          backgroundColor:
            that.active == 6
              ? "rgba(111,111,111,0.8)"
              : "rgba(235, 107, 105, 0)",
          padding: 0,
          borderWidth: 0,
          // borderColor: 'rgba(235, 107, 105, 0)',
          shape: "",
          shadow: false,
          shared: true,
          hideDelay: 10,
          style: {
            color: "#fff",
            cursor: "default",
            fontSize: "10px",
            pointerEvents: "none",
            whiteSpace: "nowrap"
          },
          // positioner() {
          //   return that.tipPositon;
          // },
          formatter() {
            // console.log(that.chart.get('max'));
            // 从博时黄金过来时，保留4位小数
            // if (that.fromGold) {
            //   return `<span style="font-size:10px">${
            //     data.date[this.x]
            //   }</span><br>${formatnumber(Number(this.y), 4)}`;
            // } else if (that.active == 3) {
            //   return (
            //     `<span style="font-size:10px">${
            //       data.date[this.x]
            //     }</span><br><span style="color:#ff1338;font-size:16px">●</span>本基金收益率:${formatnumber(
            //       Number(this.y),
            //       2
            //     )}` +
            //     `%` +
            //     `<br><span style="color:#7EA9E4;font-size:16px">●</span>同类基金平均:${formatnumber(
            //       Number(that.contrast.value[this.x]),
            //       2
            //     )}` +
            //     `%` +
            //     `<br><span style="color:#7EA9E4;font-size:16px">●</span>${
            //       that.compareName
            //     }:${formatnumber(Number(that.compare.value[this.x]), 2)}` +
            //     `%`
            //   );
            // } else if (that.active == 4) {
            //   return (
            //     `<span style="font-size:10px">${
            //       data.date[this.x]
            //     }</span><br>${formatnumber(Number(this.y), 3)}` + `%`
            //   );
            // } else if (that.active == 6) {
            //   return (
            //     `<span style="font-size:12px">${
            //       data.date[this.x]
            //     }</span><br><span style="color:#eb6b69;font-size:16px">●</span>
            //     ${that.combinationName}:${formatnumber(Number(this.y), 2)}` +
            //     `%`
            //     // + `<br><span style="color:#ff1338;font-size:16px">●</span>本基金业绩比较基准:${formatnumber(Number(that.contrast.value[this.x]), 2)}` + `%`
            //   );
            // } else {
            //   return `<span style="font-size:10px">${
            //     data.date[this.x]
            //   }</span><br>${formatnumber(Number(this.y), that.dataLength)}`;
            // }

            // console.log(this.points);
            let yList = [];
            this.points.forEach(element => {
              yList.push(element.point.y);
            });
            that.$parent.updateShowInfo(yList);
            that.tipPositon = { x: 0, y: this.points[0].point.plotY - 15 };
            return [
              `<div style="background-color: #ff0000;border-radius: 5px;font-size: 10px;">${
                that.data.date[this.x]
              }</div>`,
              `<div style="position: absolute; left: -${this.points[0].point
                .plotX +
                30}px; color:#000000; background-color: #ff0000;color:#ffffff;border-radius: 5px;font-size: 10px;">${
                this.points[0].point.y
              }%</div>`
            ];
            // ["<b>" + that.data.date[this.x] + "</b>"].concat(
            //   this.points
            //     ? [this.points[0].point.y]
            //     : []
            // );
          },
          useHTML: true,
          split: true
        },
        plotOptions: {
          series: {
            marker: {
              radius: 0,
              symbol: "circle",
              states: {
                hover: {
                  radiusPlus: 4,
                  lineWidthPlus: 2
                }
              }
            },
            states: {
              hover: {
                lineWidth: 2
              }
            },
            animation: update ? false : true
          }
        },

        series: this.getSeries

        // series: [
        //   {
        //     // type: 'areaspline',
        //     data: data.value,
        //     fillColor: {
        //       //设置折线下方背景渐变
        //       linearGradient: { x1: 0, y1: 0, x2: 0, y2: 1 },
        //       stops: [
        //         [0, "rgba(255,184,184,0.50)"],
        //         [0.3, "rgba(255,213,213,0.50)"],
        //         [1, "rgba(255,255,255,0.50)"]
        //       ]
        //     },
        //     color: themeColor,
        //     showInLegend: false,
        //     pointStart: 0
        //   },
        //   {
        //     type: "line",
        //     color: "#7EA9E4",
        //     lineWidth: 1,
        //     data: contrast.value
        //   }
        // ]
      });
    }
  }
};
</script>
